  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-amarillo:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-amarillo-contacto {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 900;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-amarillo-contacto:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 900;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-verde:hover {
  background-color: #619800;
  cursor: pointer;
}

.boton-rojo {
  background-color: red;
  color: #FFFFFF;
  font-weight: 900;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-rojo:hover {
  background-color: #e60000;
  cursor: pointer;
}

.ver-todas {
  justify-content: left;
  font-size: larger;
}

.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode h2,
.dark-mode h1,
.dark-mode h4 {
  color: #a7a7a7;
}
.dark-mode .contenedor-anuncio {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark-mode .contenedor-anuncio .precio {
  color: #71B100;
}
.dark-mode .formulario label,
.dark-mode .formulario legend,
.dark-mode .formulario p {
  color: #a7a7a7;
}
.dark-mode .formulario input:not([type=submit]),
.dark-mode .formulario textarea,
