  filter: brightness(0) invert(1);
}

.footer {
  width: 100%;
  padding: 5rem, 2;
  background-color: #333333;
  margin-bottom: 0;
}
.footer .contenedor-footer {
  padding-top: 2.5rem;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
  font-size: 2rem;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.navegacion a:hover {
  color: #FFFFFF;
}

/** Media Queries **/
.no-webp .inicio {
  background-image: url(../img/fondoengranajes.jpg);
  background-repeat: no-repeat;
  background-position: left;
  filter: brightness(50%);
}

.webp .inicio {
  background-image: url(../img/engranajes2.webp);
  inset: 0;
  background-position: left;
}

.header {
  background-color: #333333;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
.header.inicio::before {
  filter: brightness(50%);
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: left;
  color: rgb(49, 105, 49);
  font-weight: 700;
  font-size: 5rem;
  line-height: 2;
  max-width: 70rem;
  max-width: 40rem;
}
.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}

.dark-mode-boton {
  width: 4rem;
  margin-top: 3rem;
  margin-bottom: 2rem;
  filter: invert(100%);
}
.dark-mode-boton:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .dark-mode-boton {
    margin-top: 0;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.barra .logo-header {
  max-width: 100%;
  width: 28rem;
  height: auto;
}

.principal a {
  font-size: 3rem;
}

.navegacion {
  transition: 0.3s ease-in-out, opacity 0.3s ease-in-out linear, height 0.5s;
  opacity: 0;
  height: 0;
  overflow: hidden;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    visibility: visible;
    opacity: 1;
    height: auto;
    visibility: visible;
  }
}
.navegacion a {
  color: rgb(249, 94, 10);
  text-decoration: none;
  font-size: 2.2rem;
  font-weight: 700;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 1.8rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}

